/* All of modules used*/

import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {HttpModule} from "@angular/http";
import {Routes,RouterModule} from "@angular/router";
import {MatCardModule} from '@angular/material/card'
import {MatButtonModule, MatIconModule,MatDatepickerModule,MatNativeDateModule} from "@angular/material";
import {MatMenuModule} from '@angular/material/menu';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatToolbarModule} from '@angular/material/toolbar';
import {MatSelectModule} from '@angular/material/select';
import { HttpClientModule } from '@angular/common/http';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
import { FormsModule} from '@angular/forms';
import {SocialLoginModule, AuthServiceConfig, GoogleLoginProvider, FacebookLoginProvider,} from "angular5-social-login";
import 'hammerjs'
import { AgmCoreModule } from '@agm/core';

/* All of components used*/

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { ActualitesComponent } from './actualites/actualites.component';
import { ActivitesComponent } from './activites/activites.component';
import { EtudesComponent } from './etudes/etudes.component';
import { PartenairesComponent } from './partenaires/partenaires.component';
import { ContactsComponent } from './contacts/contacts.component';
import { AccueilComponent } from './accueil/accueil.component';
import { ActivitesDetailsComponent } from './activites-details/activites-details.component';
import { SignInComponent } from './sign-in/sign-in.component';
import { TestComponent } from './test/test.component';

/* All of services used*/
import {ActivitesServices} from "./Services/ActivitesServices";
import {ActualitesServices} from "./Services/ActualitesServices";
import {EtudesServices} from "./Services/EtudesServices";
import {MembresServices} from "./Services/MembresServices";
import {UserServices} from "./Services/UserServices";
import { MembresComponent } from './membres/membres.component';
import { GalerieComponent } from './galerie/galerie.component';


export function getAuthServiceConfigs() {
  let config = new AuthServiceConfig(
    [
      {
        id: FacebookLoginProvider.PROVIDER_ID,
        provider: new FacebookLoginProvider('542073729507181')
      },
      /*{
        id: GoogleLoginProvider.PROVIDER_ID,
        provider: new GoogleLoginProvider("Your-Google-Client-Id")
      },*/
    ]
);
  return config;
}

const routes: Routes = [
  {'path':'accueil', component:AccueilComponent},
  {'path' : 'contacts', component : ContactsComponent},
  {'path': 'activites', component : ActivitesComponent},
  {'path': 'galerie', component : GalerieComponent},
  {'path': 'etudes', component : EtudesComponent},
  {'path': 'partenaires', component : PartenairesComponent},
  {'path': 'membres',component:MembresComponent},
  {'path': 'detailsActivites/:id',component:ActivitesDetailsComponent},
  {'path': 'signin',component:SignInComponent},
  {'path': '', redirectTo : 'accueil', pathMatch : 'full' }]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    ActualitesComponent,
    ActivitesComponent,
    EtudesComponent,
    PartenairesComponent,
    ContactsComponent,
    AccueilComponent,
    ActivitesDetailsComponent,
    SignInComponent,
    TestComponent,
    MembresComponent,
    GalerieComponent,

  ],
  imports: [
    BrowserModule,RouterModule.forRoot(routes),FormsModule,HttpClientModule,Ng4LoadingSpinnerModule,
    HttpModule,MatCardModule,MatButtonModule,MatMenuModule,MatIconModule,
    BrowserAnimationsModule,MatToolbarModule,MatSelectModule,MatNativeDateModule,MatDatepickerModule,
    SocialLoginModule,AgmCoreModule.forRoot({
      apiKey: 'AIzaSyCLHBKkXHyptL1MfQRjyRbYOdxoA_FQEd0'
    })
  ],
  providers: [ActivitesServices,ActualitesServices,EtudesServices,MembresServices,UserServices,{
    provide: AuthServiceConfig,
    useFactory: getAuthServiceConfigs
  }],
  bootstrap: [AppComponent]
})
export class AppModule { }



