import { Component, OnInit } from '@angular/core';
import {ActivitesServices} from "../Services/ActivitesServices";
import {Router} from "@angular/router";

@Component({
  selector: 'app-activites',
  templateUrl: './activites.component.html',
  styleUrls: ['./activites.component.css']
})
export class ActivitesComponent implements OnInit {

  activites:any=[];
  constructor(private ActService:ActivitesServices,
              private  router:Router) { }

  ngOnInit() {
    this.ActService.getAllActivities()
      .subscribe(data=>this.activites=data);
  }

  detailsActivites(id:number){
     this.router.navigate(["/detailsActivites",id]);
  }



}



