import { Component, OnInit } from '@angular/core';
import {ActivitesServices} from "../Services/ActivitesServices";
import {ActivatedRoute, Router} from "@angular/router";

@Component({
  selector: 'app-activites-details',
  templateUrl: './activites-details.component.html',
  styleUrls: ['./activites-details.component.css']
})
export class ActivitesDetailsComponent implements OnInit {
activite={};
id:number;
  constructor(private  activitesService:ActivitesServices,
              private route:ActivatedRoute,
              private router:Router) { }

  ngOnInit() {
    this.id=+this.route.snapshot.params['id'];
    this.activitesService.getOneActivite(this.id)
    .subscribe(data=>this.activite=data);
  }

}



